
.box {
    position: relative;
    display: flex;  
    flex-wrap: wrap;
    width: 90%; 
    padding: 50px 00px;
    margin-top: 60px ;
    background-color: #fff;
    border-radius: 5px; 
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.454);
    border-radius: 5px;  
  }
   
  .box::after {
    content: "";
    border-radius: 5px;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3); 
  }


  .box>img{
    width: 45%;  
    /* border: 2px solid black; */
  }


  @media screen and (min-width:100px) and (max-width:920) {
    .box{ 
        display: grid;  
        border: 3px solid blue; 
    }
    
  }
